/*
*
* ==========================================
* GENERAL
* ==========================================
*
*/

section {
    overflow: hidden;
    position: relative;
}



/*
*
* ==========================================
* META COMPONENTS
* ==========================================
*
*/
.category-link {
    padding: 0.3rem 1rem;
    color: #fff;
    background: $brand-primary;
    text-transform: uppercase;
    letter-spacing: 0.1rem;
    font-size: 0.7rem;

    &:hover, &:focus {
        color: #fff;
        text-decoration: none;
    }
}

.meta-link {
    text-transform: uppercase;
    color: inherit;
    font-size: 0.75rem;
    transition: all 0.3s;

    &:hover, &:focus {
        text-decoration: none;
    }
}



/*
*
* ==========================================
* NAVBAR
* ==========================================
*
*/
.navbar {
    z-index: 98;
}


.navbar-toggler {
    outline: none !important;
    border: none !important;
}

.navbar-toggler:focus {
    border: none;
}

.navbar-toggler span {
    width: 25px;
    margin: 6px auto;
    display: block;
    height: 2px;
    background: #333;
    transition: all 0.2s;

    &:first-of-type {
        transform-origin: bottom left;
        width: 12px;
    }
    &:nth-of-type(2) {
        width: 20px;
    }
    &:last-of-type {
        transform-origin: top left;
        width: 16px;
    }
}

.navbar-toggler.active span:first-of-type {
    transform: rotate(45deg) translate(-1px);
    width: 24.5px !important;
}

.navbar-toggler.active span:last-of-type {
    width: 24.5px !important;
    transform: rotate(-45deg) translate(-1.5px);
}

.navbar-toggler.active span:nth-of-type(2) {
    opacity: 0;
}

.navbar {
    .dropdown-menu {
        @include media-breakpoint-up(lg) {
            display: block;
        }
        visibility: hidden;
        opacity:0;
        transform: translateY(0.5rem);
        transition:.2s ease all;
    }
    .dropdown-menu.show {
        display: block;
        visibility: visible;
        opacity:1;
        transform: translateY(0px);
        transition:.2s ease all;
    }
}



/*
*
* ==========================================
* POST COMPONENTS
* ==========================================
*
*/
.post-thumnail {
    position: relative;

    &::before {
        content: '';
        display: block;
        width: 100%;
        height: 50%;
        background: linear-gradient(to top, #fff, rgba(255, 255, 255, 0));
        position: absolute;
        bottom: 0;
        left: 0;
    }
}

.post-trending {
    position: relative;

    &::before {
        content: '';
        display: block;
        border-top: 3rem solid $info;
        border-left: 3rem solid transparent;
        position: absolute;
        top: 0;
        right: 0;
    }

    &::after {
        content: '\f0e7';
        display: block;
        color: #fff;
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 900;
        font-size: 0.8rem;
        position: absolute;
        top: 0.4rem;
        right: 0.5rem;
        z-index: 99;
    }
}



/*
*
* ==========================================
* CATEGORY COMPONENTS
* ==========================================
*
*/
.category-block {
    padding-bottom: 60%;
    display: block;
    position: relative;

    &::before {
        content: '';
        width: calc(100% - 1rem);
        height: calc(100% - 1rem);
        border: 1px solid #fff;
        position: absolute;
        top: 0.5rem;
        left: 0.5rem;
        z-index: 999;
        display: block;
    }

    &-sm {
        padding-bottom: 0;
        padding: 1rem;

        .category-block-title {
            position: relative;
            width: 100%;
            display: block;
            transform: none;
            z-index: 99;
            text-align: center;
            top: auto;
            left: auto;
        }
    }


    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: #000;
        opacity: 0.1;
    }

    &-title {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 99;
        transform: translate(-50%, -50%);
        background: #fff;
        color: #333;
        padding: 0.5rem 1rem;
        text-transform: uppercase;
        letter-spacing: 0.1em;
        font-size: 0.8rem;
        transition: all 0.3s;
    }

    &:hover, &:focus {
        text-decoration: none;
        .category-block-title {
            background: $brand-primary;
            color: #fff;
        }
    }
}



/*
*
* ==========================================
* SIDEBAR COMPONENTS
* ==========================================
*
*/
.sidebar-tag-link {
    padding: 0.4rem 0.8rem;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    color: $gray-700;
    font-weight: 400;
    font-size: 0.75rem;
    border: 1px solid $gray-300;
    transition: all 0.3s;
    display: block;

    &:hover, &:focus {
        border-color: $brand-primary;
        background: $brand-primary;
        color: #fff;
        text-decoration: none;
    }

}


/*
*
* ==========================================
* INSTAGRAM BLOCKS
* ==========================================
*
*/
.instagram-section-title {
    padding: 0.5rem 1.5rem;
    background: #000;
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 999;
}

.instagram-item {
    position: relative;

    &::after {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: radial-gradient(circle, rgba(0, 0, 0 , 0.1), rgba(0, 0, 0, 0.6));
    }

    &-overlay {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: rgba($brand-primary, 0.4);
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        transition: all 0.3s;
        opacity: 0;
        z-index: 99;
        font-size: 1.5rem;
    }

    &:hover {
        .instagram-item-overlay {
            opacity: 1;
        }
    }
}
